var _this;
import wInput from '../../components/watch-login/watch-input.vue' //input
import wButton from '../../components/watch-login/watch-button.vue' //button
export default {
	data() {
		return {
			//logo图片 base64
			phoneData:'', //用户/电话
			passData:'', //密码
			isRotate: false, //是否加载旋转
			log:false,
			user:[],
			playlist:[],
			swiperList:[]
		}
	},
	components:{
		wInput,
		wButton,
	},
	comments:{
		bgimage(){
			return ''
		}
	},
	filters:{
	},
	onLoad() {
		// this.login()
		this.isLogin()
		// this.getLoGin('18843058202','WWXX19980120')
	},
	mounted() {
		_this= this;
	},
	methods: {
		getsonglist(id){
			console.log(id)
			uni.navigateTo({
			    url: "../playlist/playlist?id="+id
			});
		},
		time(time){
			let d = new Date(time); 
			return d.getFullYear() + "." + (d.getMonth()+1) + '.' + d.getDate()
		},
		startLogout(){
			this.isRotate = true
			this.$login.setLogin({})
			this.log = false
			this.isRotate = false
		},
		getUserDetail(uid){
			this.$request({
				url:'/user/detail?uid='+uid
			}).then((data)=>{
				if(data.statusCode == 200){
					console.log(data.data,'用户信息')
					this.getUserPlaylist(uid)
					// this.getUserSubcount()
				}
			})
		},
		getUserPlaylist(uid){
			this.$request({
				url:'/user/playlist?uid='+uid
			}).then((data)=>{
				if(data.statusCode == 200){
					let swiperList = data.data.playlist
					this.playlist = swiperList
					let i = 0
					let list = []
					swiperList = swiperList.map((item,index)=>{
						if(i < 2){
							list.push(item)
							i++
						}
						if( i ==2 || index == swiperList.length - 1){
							let lists = list
							i = 0
							list = []
							return lists
						}
					})
					swiperList = swiperList.filter(item => item)
					this.swiperList = swiperList
					console.log('用户歌单',swiperList)
					this.$login.setUserMessage({
						user:this.user,
						message:this.playlist
					})
				}
			})
		},
		getLoGin(phone,password){
			this.isRotate=true
			this.$request({
				url:'/login/cellphone?phone='+phone+'&password='+password
			}).then((data)=>{
				this.isRotate=false
				if(data.data.code == 200){
					this.log = true
					this.$login.setLogin({
						phoneData:this.phoneData,
						passData:this.passData,
						token:data.data.token
					})
					let user = data.data
					let birthday = this.time(user.profile.birthday)
					user = {
						...user,
						birthday : birthday
					}
					this.user = user
					this.getUserDetail(user.profile.userId)
					console.log(user,'登录信息')
				}else if(data.data.code == 509){
					uni.showToast({
					    icon: 'none',
						position: 'bottom',
					    title: '密码输入错误次数太多请稍后再试'
					});
				}else{
					uni.showToast({
					    icon: 'none',
						position: 'bottom',
					    title: '密码错误'
					});
				}
			})
		},
		isLogin(){
			let value = this.$login.getLogin()
			if(value.phoneData && value.passData){
				console.log(this.$login.LOGIN())
				this.getLoGin(value.phoneData,value.passData)
			}
		},
		startLogin(){
			//登录
			if(this.isRotate){
				//判断是否加载中，避免重复点击请求
				return false;
			}
			if (this.phoneData.trim().length <11) {
			     uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '请输入正确的手机号'
			    });
			    return;
			}
		    if (this.passData.trim().length < 1) {
		        uni.showToast({
		            icon: 'none',
					position: 'bottom',
		            title: '密码不能为空'
		        });
		        return;
		    }
			this.getLoGin(this.phoneData,this.passData)
		}
		
	}
}
